import { Input, Component, OnInit} from '@angular/core';

import { PublicacionesServiceService } from '../../services/Publicaciones-service.service';
import {StorageServiceService} from '../../services/storage-service.service';
import { ImageResponse } from '../../modelos/Response/ImageResponse.model';
import { ServiceSpaceMultimedia } from '../../modelos/servicespacemultimedia.model';
import { GenericStorageService } from '../../services/genericstorage.service';
import { UrlBackendServices } from '../../services/AppServiceGlobal';

import { FileUploader, FileItem, ParsedResponseHeaders, FileUploaderOptions } from 'ng2-file-upload/ng2-file-upload';
import {ImagePicker, ImagePickerOptions} from '@ionic-native/image-picker/ngx';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-upload-images2',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.scss']
})

export class UploadComponent implements OnInit 
{
  @Input() ServiceUrl : string;
  @Input() ServiceIndex : number;
  @Input() NombreTipoServicio : string;

  UrlBackend = UrlBackendServices;
  results$: Observable<ImageResponse>;
  InfoServiceSpaceMultimedia : ServiceSpaceMultimedia[];
  imageURI:any;
  imageFileName:any;
  uploader: FileUploader;
  uploaderOptions: FileUploaderOptions;

  interval;
  mensaje: any = [];

  constructor(private storage:StorageServiceService, 
    private serviceImages: PublicacionesServiceService,
    private genStorageServiceSpaceMultimedia : GenericStorageService<ServiceSpaceMultimedia[]>,
    private imagePicker: ImagePicker,
    private camera: Camera
    ){ }  

  ngOnInit() 
  {
        this.ActualizarImagenes();
        this.startTimer();
  }

  public uploadFile(imgbase64: string )
  {
        if(!imgbase64 || imgbase64 == null || imgbase64 == "" || imgbase64.length < 10)
            return;  

        this.serviceImages.ImageLoader(this.ServiceIndex, this.NombreTipoServicio, imgbase64).subscribe(respuesta=>
        {
            var registroResponse: ImageResponse = respuesta;
            if(registroResponse != null && registroResponse.Exitoso)
            {                
                this.OnImageUploaded(registroResponse);
            }
            else{
                this.mensaje = [{ id:2, type:'warning', message: registroResponse.MensajeResultado }];
            }
        },
        error=>
        {
            this.mensaje = [{ id:2, type:'danger', message:error.statusText }];
        });
  }

  public TomarFoto()
  {
    const options: CameraOptions = 
    {
        quality: 50,
        destinationType : this.camera.DestinationType.DATA_URL,
        encodingType: this.camera.EncodingType.JPEG,
        mediaType: this.camera.MediaType.PICTURE,
        sourceType: this.camera.PictureSourceType.CAMERA
    };

    this.camera.getPicture(options).then((imageData) =>
    {
        this.uploadFile(imageData);
    },
    (error) =>{ console.log(error); });
  }

  public CargarImagenes()
  {
    const options: ImagePickerOptions = 
    {
        quality: 50,
        maximumImagesCount: 10,
        outputType: 1
    };

    this.imagePicker.getPictures(options).then((results) =>
    {
      for(var i = 0; i<results.length; i++)
      {
            this.uploadFile(results[i]);
      }
    },
    (error) =>{ console.log(error); });
  }

  public OnImageUploaded(resultado : ImageResponse)
  {
      if(resultado && resultado != null && resultado.Exitoso)
      {  
          var InfoServiceSpaceMultimedia	: ServiceSpaceMultimedia[] = []
          var genStorageServiceSpaceMultimedia : GenericStorageService<ServiceSpaceMultimedia[]> = new GenericStorageService<ServiceSpaceMultimedia[]>();
          var NombreTipoServicio : string = resultado.ServiceName;
          var ServiceIndex : string = resultado.Index;

          InfoServiceSpaceMultimedia = genStorageServiceSpaceMultimedia.getInformation("InfoServiceSpaceMultimedia_" + NombreTipoServicio + "_" + ServiceIndex);
          if(!InfoServiceSpaceMultimedia || InfoServiceSpaceMultimedia == null)
              InfoServiceSpaceMultimedia = [];

          var serviceSpaceMultimedia = new ServiceSpaceMultimedia();
          serviceSpaceMultimedia.Content = resultado.ImageUrl;

          InfoServiceSpaceMultimedia.push(serviceSpaceMultimedia);
          genStorageServiceSpaceMultimedia.setInformation("InfoServiceSpaceMultimedia_" + NombreTipoServicio + "_" + ServiceIndex, InfoServiceSpaceMultimedia);
      }
  }

  public startTimer() 
  {
    this.interval = setInterval(() => 
    {
        this.ActualizarImagenes();
    },2000);
  }

  public pauseTimer() {
    clearInterval(this.interval);
  }

  public ActualizarImagenes()
  {
      this.pauseTimer();

      if(!this.InfoServiceSpaceMultimedia || this.InfoServiceSpaceMultimedia == null)
          this.InfoServiceSpaceMultimedia = [];

      var listadoAuxiliar =  this.genStorageServiceSpaceMultimedia.getInformation("InfoServiceSpaceMultimedia_" + this.NombreTipoServicio + "_" + this.ServiceIndex);   
      if(listadoAuxiliar && listadoAuxiliar != null)
      {

          listadoAuxiliar.forEach(image => {

              var infoImage = this.InfoServiceSpaceMultimedia.find(x=>x.Content == image.Content);
              if(!infoImage || infoImage == null)
              {

                  this.InfoServiceSpaceMultimedia.push(image);
              }
          });
      }      
      
      this.startTimer();



  }

  public EliminarImagen(infoImage : any)
  {
      var listadoAuxiliar =  this.genStorageServiceSpaceMultimedia.getInformation("InfoServiceSpaceMultimedia_" + this.NombreTipoServicio + "_" + this.ServiceIndex);   
      var posArrayStorage = listadoAuxiliar.findIndex(x=>x.Content == infoImage.Content);
      if(posArrayStorage != -1)
      {
          listadoAuxiliar.splice(posArrayStorage,1);
          this.genStorageServiceSpaceMultimedia.setInformation("InfoServiceSpaceMultimedia_" + this.NombreTipoServicio + "_" + this.ServiceIndex, listadoAuxiliar);
      }    

      var posArray = this.InfoServiceSpaceMultimedia.findIndex(x=>x.Content == infoImage.Content);
      if(posArray != -1)
      {
          this.InfoServiceSpaceMultimedia.splice(posArray,1);
      }    
  }
}








