import { Component, OnInit, EventEmitter, Output, Input } from '@angular/core';
import { ServiceFavorite } from '../../modelos/servicefavorite.model';
import { SearchServiceService } from '../../services/Search-service.service';
import { RegisterResponse } from '../../modelos/Response/RegisterResponse.model';
import { GenericStorageService } from '../../services/genericstorage.service';

@Component({
  selector: 'app-agregarfavoritos',
  templateUrl: './agregarfavoritos.component.html',
  styleUrls: ['./agregarfavoritos.component.scss'],
})
export class AgregarfavoritosComponent implements OnInit {
  corazon = '';
  @Input()parameterfavorito: ServiceFavorite = new ServiceFavorite();
  
  constructor(
    private servicePublicaciones: SearchServiceService,
    private genStorageFavoritos : GenericStorageService<ServiceFavorite[]>
  ){}

  ngOnInit() {
    this.ValidarEstadoFavorito();
  }

  AddorRemove() {
    this.parameterfavorito.IsFavorite = !this.parameterfavorito.IsFavorite;
    this.servicePublicaciones.AgregarFavoritos(this.parameterfavorito).subscribe(respuesta => 
    {
        var registroResponse: RegisterResponse = respuesta;
    });

    this.ActualizarListaLocal();
  }

  ValidarEstadoFavorito()
  {
    this.parameterfavorito.IsFavorite = false;
    var listFavorite: ServiceFavorite[] = [];
    listFavorite = this.genStorageFavoritos.getInformation('Cache_ServiceFavoriteList');

    if(listFavorite == null)
    {
      return;
    }

    var index = listFavorite.findIndex(x=>x.IdService == this.parameterfavorito.IdService && x.TypeService == this.parameterfavorito.TypeService);
    this.parameterfavorito.IsFavorite = (index != -1) ?  listFavorite[index].IsFavorite : false;   
    console.log(this.parameterfavorito);
  }

  ActualizarListaLocal()
  {
      var listFavorite: ServiceFavorite[] = [];
      listFavorite = this.genStorageFavoritos.getInformation('Cache_ServiceFavoriteList');

      if(listFavorite == null)
          listFavorite = []

      if(this.parameterfavorito.IsFavorite)
      {
          listFavorite.push(this.parameterfavorito);
      }
      else
      {
        var index = listFavorite.findIndex(x=>x.IdService == this.parameterfavorito.IdService && x.TypeService == this.parameterfavorito.TypeService);
        if(index != -1)
            listFavorite.splice(index,1);
      }
      this.genStorageFavoritos.setInformation('Cache_ServiceFavoriteList', listFavorite);
  }
}
