import { Injectable } from '@angular/core';
import { Guid } from "guid-typescript";
import { HttpClient, HttpHeaders } from '@angular/common/http';
import {Observable} from 'rxjs/Observable';

import { CacheStorageService } from '../services/cachestorage.service';
import { StorageServiceService } from '../services/storage-service.service';
import { GenericStorageService } from '../services/genericstorage.service';

import { Service } from '../modelos/service.model';
import { CoWorkingSpace } from '../modelos/coworkingspace.model';
import { ServiceSpaceItems } from '../modelos/ServiceSpaceItems.model';
import { ServiceSpaceContract } from '../modelos/servicespacecontact.model';
import { ServiceBenefit } from '../modelos/servicebenefit.model';
import { ServiceSpaceMultimedia } from '../modelos/servicespacemultimedia.model';
import { ServiceTransport } from '../modelos/servicetransport.model';
import { ServiceSchedule } from '../modelos/serviceschedule.model';
import { ServiceAditionals } from '../modelos/serviceaditionals.model';
import { ServiceSpaceBenefit } from '../modelos/servicespacebenefit.model';
import { Coordinate } from '../modelos/coordinate.model';
import { RegisterResponse } from '../modelos/Response/RegisterResponse.model';
import { MultimediaType } from '../modelos/multimediatype.model';
import {UrlBackendServices} from './AppServiceGlobal';
import { ImageResponse } from '../modelos/Response/ImageResponse.model';
import { AccommodationSpace } from '../modelos/accommodationspace.model';
import { ExclusivenessType } from '../modelos/exclusivenesstype.model';
import { SpaceType } from '../modelos/spacetype.model';
import { RoomSpaceItems } from '../modelos/RoomSpaceItems.model';
import { RoomSpace } from '../modelos/RoomSpace.model';
import { HotelSpace } from '../modelos/hotelspace.model';
import { BasicInformation } from '../modelos/BasicInformation.model';
import { GenericResponse } from '../modelos/Response/GenericResponse.model';
import { SimpleGenericResponse } from '../modelos/Response/SimpleGenericResponse.model';
import { ServiceFavorite } from '../modelos/servicefavorite.model';

@Injectable({
  providedIn: 'root'
})

export class SearchServiceService
{
  UrlSearch = UrlBackendServices + "/api/Search";
  UrlBasic = UrlBackendServices + "/api/";

  constructor( private http:HttpClient, private storage: StorageServiceService) {}

  public Buscar(isCW: boolean, isHT: boolean, isAC: boolean): Observable<GenericResponse<BasicInformation>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken(),
      IsCoWorking         : isCW,
      IsHotel             : isHT,
      IsAccommodation     : isAC
    };

  	return this.http.post<GenericResponse<BasicInformation>>(this.UrlSearch,consulta,{headers:headers});
  }

  public BuscarCoWorking(filters: any): Observable<GenericResponse<BasicInformation>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken(),
      IsCoWorking         : true,
      IsHotel             : false,
      IsAccommodation     : false,
      PageIndex           : filters.Paginacion,
      CoWorkingFilters    : 
      {
          MinValue              : filters.Minimo,
          MaxValue              : filters.Maximo,
          City                  : filters.Ciudad,
          IdCountry             : filters.IdCountry,
          ContractTypeList      : filters.InfoContractType,
          SpaceTypeList         : filters.InfoSpaceType
      }
    };

  	return this.http.post<GenericResponse<BasicInformation>>(this.UrlSearch,consulta,{headers:headers});
  }

  public BuscarAlojamiento(filters: any): Observable<GenericResponse<BasicInformation>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken(),
      IsCoWorking         : false,
      IsHotel             : false,
      IsAccommodation     : true,
      PageIndex           : filters.Paginacion,
      AccommodationFilters: 
      {
          MinValue              : filters.Minimo,
          MaxValue              : filters.Maximo,
          City                  : filters.Ciudad,
          IdCountry             : filters.IdCountry,
          ContractTypeList      : filters.InfoContractType,
          AccommodationSpaceTypeList: filters.InfoAccommodationSpaceType
      }
    };

  	return this.http.post<GenericResponse<BasicInformation>>(this.UrlSearch,consulta,{headers:headers});
  }

  public BuscarHotel(filters: any): Observable<GenericResponse<BasicInformation>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken(),
      IsCoWorking         : false,
      IsHotel             : true,
      IsAccommodation     : false,
      PageIndex           : filters.Paginacion,
      HotelFilters: 
      {
          MinValue              : filters.Minimo,
          MaxValue              : filters.Maximo,
          City                  : filters.Ciudad,
          IdCountry             : filters.IdCountry,
          ContractTypeList      : filters.InfoContractType,
          SpaceTypeList         : filters.InfoSpaceType
      }
    };

  	return this.http.post<GenericResponse<BasicInformation>>(this.UrlSearch,consulta,{headers:headers});
  }

  public AgregarFavoritos(paramServiceFavorite : ServiceFavorite): Observable<RegisterResponse> 
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken(),
      Parameters          :
      {
        TypeService       : paramServiceFavorite.TypeService,
        IdService         : paramServiceFavorite.IdService,
        IdUser            : this.storage.getCurrentUser().IdUser,
        IsFavorite        : paramServiceFavorite.IsFavorite
      }
    };

    return this.http.put<RegisterResponse>(this.UrlBasic + "ServiceFavorite", consulta, {headers:headers});
  }

  public ConsultarMisFavoritos(): Observable<GenericResponse<BasicInformation>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken()
    };

  	return this.http.post<GenericResponse<BasicInformation>>(this.UrlBasic + "ServiceFavorite", consulta, {headers:headers});
  }

  public MisPublicaciones(isCW: boolean, isHT: boolean, isAC: boolean): Observable<GenericResponse<BasicInformation>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = 
    {
      ApplicationId       : this.storage.getCurrentApplication(),
      IdUser              : this.storage.getCurrentUser().IdUser,
      Token               : this.storage.getCurrentToken(),
      IsCoWorking         : isCW,
      IsHotel             : isHT,
      IsAccommodation     : isAC,
      CoWorkingFilters    : { IdUser: this.storage.getCurrentUser().IdUser },
      HotelFilters        : { IdUser: this.storage.getCurrentUser().IdUser },
      AccommodationFilters: { IdUser: this.storage.getCurrentUser().IdUser }
    };

  	return this.http.post<GenericResponse<BasicInformation>>(this.UrlSearch,consulta,{headers:headers});
  }

  public GetDetailsInfo<T>(controllerName: string, IdServicio: string, Command: string = null, IdServiceType: string = null, IdServiceSpace: string = null) : Observable<GenericResponse<T>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = 
    {
      ApplicationId       : this.storage.getCurrentApplication(),
      Token               : this.storage.getCurrentToken(),
      IdService           : IdServicio,
      IdServiceType       : IdServiceType,
      IdServiceSpace      : IdServiceSpace,
      Command             : Command
    };

  	return this.http.post<GenericResponse<T>>(this.UrlBasic + controllerName, consulta, {headers:headers});
  }

  public GetDetailInfo<T>(controllerName: string, IdServicio: string, Command: string = null, IdServiceType: string = null, IdServiceSpace: string = null) : Observable<SimpleGenericResponse<T>>
  {
    const headers = new HttpHeaders({"Content-Type":"application/json",'Access-Control-Allow-Origin': '*'});
    const consulta = 
    {
      ApplicationId       : this.storage.getCurrentApplication(),
      Token               : this.storage.getCurrentToken(),
      IdService           : IdServicio,
      IdServiceType       : IdServiceType,
      IdServiceSpace      : IdServiceSpace,
      Command             : Command
    };

  	return this.http.post<SimpleGenericResponse<T>>(this.UrlBasic + controllerName, consulta, {headers:headers});
  }
}
