import { Input, Output, Component, ViewChild, OnInit} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { CacheStorageService } from './../../services/cachestorage.service';
import { GenericStorageService } from 'src/app/services/genericstorage.service';
import { AnimationRendererFactory } from '@angular/platform-browser/animations/src/animation_renderer';
import { BasicInformation } from 'src/app/modelos/BasicInformation.model';
import { ServiceFavorite } from 'src/app/modelos/servicefavorite.model';
import { AgregarfavoritosComponent } from '../agregarfavoritos/agregarfavoritos.component';
import { SearchServiceService } from 'src/app/services/Search-service.service';

@Component({
  selector: 'app-ServiceBasicView',
  templateUrl: './ServiceBasicView.component.html',
  styleUrls: ['./ServiceBasicView.scss']
})

export class ServiceBasicViewComponent implements OnInit
{
    @Input() basicInformation: BasicInformation;
    DefaultImageUrl: string;
    
    favorito : ServiceFavorite;
    @ViewChild(AgregarfavoritosComponent) compCalificacion: AgregarfavoritosComponent;

    constructor(
      private router:Router,
      private servicePublicaciones: SearchServiceService,
      private genStorageFavoritos : GenericStorageService<ServiceFavorite[]>)
    {
      this.DefaultImageUrl = "assets/images/background/SinImagen.png";
    }

    ngOnInit() 
    {
      if( this.basicInformation != null && this.basicInformation.Image != null)
      {
          var aux: string = this.basicInformation.Image;
          aux = aux.replace("thumbnail","mediana");
          aux = "http://bricks4us.azurewebsites.net/" + aux;
          this.basicInformation.Image = aux;
      }
      else if (this.basicInformation.Image == null)
      {
        this.basicInformation.Image = this.DefaultImageUrl;
      }

      this.favorito = new ServiceFavorite();
      this.favorito.IdService = this.basicInformation.IdService;
      this.favorito.TypeService = this.basicInformation.ServiceType;
      this.compCalificacion = new AgregarfavoritosComponent(this.servicePublicaciones, this.genStorageFavoritos);
    }

    VerDetalleServicio()   
    {
        //if(this.basicInformation.ServiceType != "CoWorking")
            this.router.navigate(["/VistaServicio/" + this.basicInformation.ServiceType + "/" + this.basicInformation.IdService]);
    }
}